@use 'sass:map';
@use '../../common/variables';

.room-card {
  max-width: 270px;
  display: grid;
  grid-template-rows: 1fr auto auto;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 10px 20px 0 rgba(map.get(variables.$colors, 'dark'), 0.05);

  &:focus-within {
    outline: 2px solid map.get(variables.$colors, 'dark');
  }

  &__information {
    padding: 18px 15px 17px 22px;
  }

  &__details,
  &__feedback {
    display: flex;
    justify-content: space-between;
  }

  &__number {
    padding-top: 2px;
  }

  &__price {
    padding-top: 6px;
  }

  &__details {
    padding-bottom: 10px;
  }

  &__rating {
    padding-left: 1px;
  }

  &__feedback {
    padding: 11px 0 0 2px;
    border-top: 1px solid rgba(map.get(variables.$colors, 'dark'), 0.1);
  }

  &__reviews {
    color: rgba(map.get(variables.$colors, 'dark'), 0.5);
    font-size: map.get(variables.$font-sizes, 'small');
    padding: 2px 2px 0 0;
    word-break: break-all;
  }

  &__reviews-count {
    font-size: map.get(variables.$font-sizes, 'small');
    font-weight: 700;
  }

  &__reviews-word {
    text-transform: capitalize;
  }
}
