@use 'sass:map';
@use '../../common/variables';

.colors-page {
  &__content {
    max-width: variables.$content;
    padding: 0 5px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
  }

  &__container {
    display: grid;
    grid-template-columns: 70px auto;
    grid-template-rows: 35px 35px;
    gap: 0 40px;
    font-size: map.get(variables.$font-sizes, 'medium');
  }

  &__colors {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__color {
    width: 100%;
    height: 100%;
    grid-row: 1 / -1;
    border-radius: 6px;

    &_dark-100 {
      background-color: map.get(variables.$colors, 'dark');
    }

    &_dark-75 {
      background-color: rgba(map.get(variables.$colors, 'dark'), 0.75);
      border: 1px solid map.get(variables.$colors, 'dark');
    }

    &_dark-50 {
      background-color: rgba(map.get(variables.$colors, 'dark'), 0.5);
    }

    &_dark-25 {
      background-color: rgba(map.get(variables.$colors, 'dark'), 0.25);
    }

    &_dark-5 {
      background-color: rgba(map.get(variables.$colors, 'dark'), 0.05);
    }

    &_purple {
      background-color: map.get(variables.$colors, 'primary');
    }

    &_green {
      background-color: map.get(variables.$colors, 'secondary');
    }
  }

  &__descr {
    font-weight: 700;
    align-self: end;
    padding-bottom: 2px;
  }

  // Types
  &__types {
    display: flex;
    flex-direction: column;
    gap: 50px;
    max-width: 500px;
  }

  &__type {
    display: flex;
    gap: 30px;

    &_uppercase {
      text-transform: uppercase;
    }

    &_h3 {
      max-width: 450px;
    }

    &_text {
      max-width: 380px;
    }
  }

  &__level {
    color: rgba(map.get(variables.$colors, 'dark'), 0.25);
    text-transform: capitalize;

    &_h1 {
      font-size: map.get(variables.$font-sizes, 'big');
    }

    &_h3 {
      font-size: map.get(variables.$font-sizes, 'medium');
    }

    &_h5 {
      font-size: map.get(variables.$font-sizes, 'small');
    }
  }
}
